<!--  -->
<template>
  <div>
    <div id="shadow" @click="shadowOnclick"></div>
    <div id="banner">
      <banner></banner>
    </div>

    <wrapper
      :forgetshow="forgetshow"
      :registshow="registshow"
      @login="login"
      @getForgetshow="getForgetshow"
      @getRegistshow="getRegistshow"
    ></wrapper>
    <transition enter-active-class="animated bounceIn" leave-active-class="animated bounceOut">
      <forgetpsw class="forgetpsw" v-if="forgetshow" @hideForget="hideForget"></forgetpsw>
      <regist class="regist" v-if="registshow" @hideRegist="hideRegist"></regist>
    </transition>
    <!-- <div class="leave" v-show="leaveshow">
      <leave :leaveshow="leaveshow"></leave>
    </div>-->
  </div>
</template>

<script>
import { animiteListener } from "../../js/index/login.js";
// import Leave from "../../components/index/Leave";
import banner from "../../components/index/Banner";
import wrapper from "../../components/index/Wrapper";
import forgetpsw from "../../components/index/Forgetpsw";
import regist from "../../components/index/Regist";
export default {
  data() {
    return {
      // allflag: true,
      forgetshow: false,
      registshow: false,
      // leaveshow: false
    };
  },
  created() {},
  methods: {
    shadowOnclick() {
      this.forgetshow = false;
      this.registshow = false;
    },
    login() {
      // this.allflag = false;
      this.leaveshow = true;
      this.$router.push({ path: "/leave" });
    },
    getForgetshow(forgetshow) {
      this.forgetshow = forgetshow;
    },
    hideForget(forgetshow) {
      this.forgetshow = forgetshow;
    },
    getRegistshow(registshow) {
      this.registshow = registshow;
    },
    hideRegist(registshow) {
      this.registshow = registshow;
    }
  },
  watch: {
    forgetshow(newValue, oldValue) {
      if (newValue == true) {
        var shadow = document.getElementById("shadow");
        shadow.style.display = "block";
      } else {
        var shadow = document.getElementById("shadow");
        shadow.style.display = "none";
      }
    },
    registshow(newValue, oldValue) {
      if (newValue == true) {
        var shadow = document.getElementById("shadow");
        shadow.style.display = "block";
      } else {
        var shadow = document.getElementById("shadow");
        shadow.style.display = "none";
      }
    }
  },
  components: {
    banner,
    wrapper,
    forgetpsw,
    regist
  },
  mounted() {
    animiteListener();
  }
};
</script>
<style scoped>
@import "../../style/index/login.css";
@import "../../style/index/profile.css";
@import "../../style/index/regist.css";
#shadow {
  width: 100%;
  height: 100%;
  background-color: #000;
  filter: alpha(opacity=50);
  -moz-opacity: 0.5;
  opacity: 0.5;
  position: absolute;
  left: 0px;
  top: 0px;
  display: none;
  z-index: 998;
}
.leave {
  position: relative;
  z-index: 999;
}
#banner {
  position: relative;
  z-index: 4;
}
</style>